*{
    padding: 0%;
    margin: 0;
    box-sizing: border-box;
}

html, body{
    background-color: #000000;
}

.App{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    padding-top: 50px;
    font-family: Lato, sans-serif;
}

.contenedor-principal{
    height: 550px;
    min-width: 600px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
